<template>
  <div class="add_main">
    <span class="add_item">
      <el-input placeholder="" v-model="appname">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;:</template>
      </el-input>
    </span>
    <span class="add_item">
      <el-input placeholder="" v-model="accountname">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;:</template>
      </el-input>
    </span>
    <span class="add_item">
      <el-input placeholder="" v-model="password">
        <template slot="prepend">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</template>
      </el-input>
    </span>
    <span class="add_item">
      <el-input placeholder="" v-model="repassword">
        <template slot="prepend">:</template>
      </el-input>
    </span>
    <span class="add_item">
      <el-input placeholder="" v-model="description">
        <template slot="prepend">:</template>
      </el-input>
    </span>
    <span class="add_item">
        <span>{{info}}</span>
    </span>
    <span class="add_item">
      <el-button type="danger" @click="show_index" plain></el-button>
      <el-button type="primary" @click="submit_data" plain></el-button>
    </span>
  </div>
</template>

<script>
import conf from '@/assets/conf.js'
import Qs from 'qs'
export default {
  name: 'AddItem',
  data () {
    return {
      appname: '',
      accountname: '',
      password: '',
      repassword: '',
      description: '',
      info: ' '
    }
  },
  methods: {
    show_index () {
      this.$emit('showindex', true)
    },
    submit_data () {
      let _self = this
      _self.info = ''
      if (_self.accountname === '' || _self.password === '' || _self.repassword === '') {
        _self.info = ''
        return false
      }
      if (_self.password !== _self.repassword) {
        _self.info = ''
        return false
      }
      // 
      let url = conf.url + 'secret/addsecret'
      _self.$http.post(url, Qs.stringify({ac: _self.accountname, pw: _self.password, an: _self.appname, repw: _self.repassword, desc: _self.description})).then(function (res) {
        if (res && res.status !== 200) {
          _self.info = ''
          return false
        }
        let data = res.data
        if (data && data['code'] && data['code'] !== 1) {
          _self.info = data.info ? data.info : ''
          return false
        }
        // 
        _self.info = ''
        location.reload()
        _self.$router.push({ path: '/' })
      })
    }
  }
}
</script>

<style scoped>
.add_main{
  /*border: 2px solid green;*/
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}
.add_item{
  display: block;
  margin-top: 20px;
  text-align: center;
}
</style>



